(function($){

    const $slider = $(".slider");
    $slider
        .on('init', () => {
            mouseWheel($slider)
        })
        .slick({
            dots: true,
            infinite: false,
            speed: 500,
            fade: true,
            cssEase: 'linear'
        });
    function mouseWheel($slider) {
        $(window).on('wheel', { $slider: $slider }, mouseWheelHandler)
    }
    function mouseWheelHandler(event) {
        event.preventDefault()
        const $slider = event.data.$slider
        const delta = event.originalEvent.deltaY
        if(delta > 0) {
            $slider.slick('slickPrev')
        }
        else {
            $slider.slick('slickNext')
        }
    }
    //Accordion Box
    if($('.accordion_box').length){
        $(".accordion_box").on('click', '.btn_acc', function() {
            var outerBox = $(this).parents('.accordion_box');
            var target = $(this).parents('.accordion');
            if($(this).hasClass('active')!==true){
                $(outerBox).find('.accordion .btn_acc').removeClass('active');
            }
            if ($(this).next('.content_acc').is(':visible')){
                return false;
            }else{
                $(this).addClass('active');
                $(outerBox).children('.accordion').removeClass('active-block');
                $(outerBox).find('.accordion').children('.content_acc').slideUp(300);
                target.addClass('active-block');
                $(this).next('.content_acc').slideDown(200);
            }
        });
    }
    // action contacto
    var bodyEl = document.body,
        content = document.querySelector( '.content-wrap' ),
        openbtn = document.getElementById( 'open-button' ),
        closebtn = document.getElementById( 'close-button' ),
        isOpen = false;

    function init() {
        initEvents();
    }

    function initEvents() {
        openbtn.addEventListener( 'click', toggleMenu );
        if( closebtn ) {
            closebtn.addEventListener( 'click', toggleMenu );
        }

        // close the menu element if the target itÂ´s not the menu element or one of its descendants..
        content.addEventListener( 'click', function(ev) {
            const target = ev.target;
            if( isOpen && target !== openbtn ) {
                toggleMenu();
            }
        } );
    }

    function toggleMenu() {
        if( isOpen ) {
            classie.remove( bodyEl, 'show-menu' );
        }
        else {
            classie.add( bodyEl, 'show-menu' );
        }
        isOpen = !isOpen;
    }

    init();
})(jQuery);